.aside__form {
  background-color: #ffffff;
  box-shadow: 0 0 24px 0 rgba(37, 40, 74, 0.04);
}

@media only screen and (max-width: 1024px) {
  .aside__form {
    display: none;
  }
}

.aside__form form {
  padding: 15px 30px 27px 30px;
}

.aside__form form input {
  height: 48px;
}

.aside__form form .input:before {
  top: 12px;
}

.aside__form form .btn {
  height: 65px;
}

.pagination {
  display: flex;
  align-items: center;
  margin-top: 20px;
}

@media only screen and (max-width: 1024px) {
  .pagination {
    margin-bottom: 40px;
    margin-top: 10px;
    justify-content: center;
  }
}

.pagination a.link {
  font-weight: 500;
  font-size: 16px;
  color: #161c28;
  font-family: 'Museo Sans';
  text-decoration: none;
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 4px;
  border-radius: 50%;
  transition: all 0.3s;
}

.pagination a.link.active {
  border: 1px solid #ef9a3e;
}

.pagination a.link:hover {
  background: #ef9a3e;
  color: #ffffff;
}

.pagination span {
  height: 30px;
  margin-bottom: 0;
}

.pagination .prev, .pagination .next {
  font-weight: 500;
  color: #161c28;
  display: flex;
  align-items: center;
  cursor: pointer;
  display: inline-block;
  text-decoration: none;
}

.pagination .prev i, .pagination .next i {
  margin: 0 7px;
  font-size: 10px;
}

.pagination .prev:hover, .pagination .next:hover {
  color: #ef9a3e;
}

.pagination .prev {
  margin-right: 15px;
}

.pagination .next {
  margin-left: 15px;
}

@media only screen and (max-width: 1024px) {
  .news__content__article article {
    width: 100%;
  }
}

.news__content__article article img {
  margin-bottom: 30px;
}

@media only screen and (max-width: 1024px) {
  .news__content__article article img {
    width: 100%;
  }
}

.news__content__article article span {
  color: rgba(0, 0, 0, 0.3);
}

.news__content__article article h1 {
  margin-top: 20px;
  font-size: 36px;
  line-height: normal;
  margin-bottom: 25px;
}

@media only screen and (max-width: 375px) {
  .news__content__article article h1 {
    font-size: 24px;
    font-weight: 600;
  }
}

.news__content__article article hr {
  border: none;
  border-top: 1px solid rgba(0, 0, 0, 0.12);
  margin-bottom: 25px;
  margin-top: 25px;
}

.news__content__article article footer p {
  font-weight: 500;
  color: #161c28;
}

.news__content__article article footer p a {
  color: #6e91d7;
  margin-left: 10px;
}

.contact {
  padding-top: 50px;
  padding-bottom: 100px;
  background: #f5f5f5;
}

@media only screen and (max-width: 1024px) {
  .contact {
    padding-bottom: 60px;
    padding-top: 30px;
  }
}

.contact h1 {
  font-size: 48px;
  font-family: 'Museo Sans';
  font-weight: 900;
}

@media only screen and (max-width: 1024px) {
  .contact h1 {
    margin-bottom: 20px;
  }
}

@media only screen and (max-width: 375px) {
  .contact h1 {
    font-size: 32px;
  }
}

.contact .form__social {
  margin-bottom: 40px;
}

@media only screen and (max-width: 1024px) {
  .contact .form__social {
    justify-content: flex-start;
    display: block;
  }
}

.contact .form__social a {
  color: #161c28;
  text-decoration: none;
}

.contact .form__social a:hover span {
  text-decoration: underline;
}

.contact .form__social i {
  text-decoration: none;
}

@media only screen and (max-width: 1024px) {
  .contact .form__social span {
    display: block;
  }
}

.contact iframe {
  box-shadow: 16px 16px 0 0 #ef9a3e;
  border: solid 8px #ffffff !important;
}

@media only screen and (max-width: 1024px) {
  .contact iframe {
    width: 730px;
    height: 430px;
    box-shadow: 10px 10px 0 0 #ef9a3e;
    border: solid 8px #ffffff;
  }
}

@media only screen and (max-width: 375px) {
  .contact iframe {
    width: 345px;
    height: 240px;
    box-shadow: 5px 5px 0 0 #ef9a3e;
    border: solid 5px #ffffff;
  }
}
